.main-slider{
    &__text{
        text-align: center;
        margin-left: 150px;
        width: 550px;
        padding: 20px 0;
        .Btn{
            margin-top: 50px;
            padding: 10px 40px;
        }
        &-inner{
            font-size: 25px;
            text-transform: uppercase;
            line-height: 35px;
            font-weight: 700;
            p{
                margin: 0;
            }
        }
    }
    .swiper-slide{
        min-height: 487px;
        background-position-x: center;
        background-size: cover;
        background-color: #f2f2f2;
        display: flex;
        align-items: center;
    }
    .swiper-button{
        &-prev, &-next{
            top: auto;
            bottom: 40px;
            &::after{
                transition: all .3s ease-out;
            }
            &:hover{
                bottom: 28px;
                width: 30px;
                height: 60px;
                &::after{
                    font-size: 60px;
                    transition: all .3s ease-out;
                }
            }
        }
        &-next{
            right: 70px;
        }
        &-prev{
            left: auto;
            right: 140px;
        }
    }
    .swiper-pagination{
        bottom: 28px;
        padding-left: 70px;
        text-align: left;
    }
}
@media(max-width: 1890px){
    .main-slider{
        &__text{
            margin-left: 10px;
        }
    }
}
@media(max-width: 1650px){
    .main-slider__text{
        width: 450px;
    }
}
@media(max-width: 1500px){
    .main-slider {
        .swiper-slide{
            min-height: 350px;
        }
    }
}
@media(max-width: 1199px){
    .main-slider {
        .swiper-slide{
            min-height: 380px;
        }
    }
}
@media(max-width: 991px){
    .main-slider {
        .swiper-slide{
            min-height: 312px;
        }
        &__text{
            width: 310px;
            &-inner{
                font-size: 21px;
            }
        }
        .swiper-pagination{
            padding-left: 11px;
            bottom: 20px;
        }
        .swiper-button-prev, .swiper-button-next{
            display: none;
        }
    }
}
@media(max-width: 767px) {
    .main-slider {
        margin: 0 -15px;
        &__text {
            margin-left: 40px;
        }
        &__text .Btn {
            margin-top: 20px;
        }
    }
}
@media(max-width: 600px){
    .main-slider{
        /* overflow: inherit; */
        margin-left: -15px;
        margin-right: -15px;
        .swiper-slide{
            min-height: 433px;
            align-items: flex-start;
            justify-content: center;
        }
        &__text{
            margin-left: 0;
            padding: 30px 10px;
            width: 100%;
            .Btn{
                margin-top: 15px;
            }
        }
        .swiper-slide{
            background-position: center bottom;
            background-repeat: no-repeat;
            min-height: 600px;
        }
    }
}
@media(max-width: 500px){
    .main-slider {
        .swiper-slide{
            min-height: 520px;
        }
        &__text-inner p {
            font-size: 19px;
        }
    }
}
@media(max-width: 400px){
    .main-slider {
        .swiper-slide{
            min-height: 480px;
        }
    }
}
@media(max-width: 350px){
    .main-slider {
        .swiper-slide{
            min-height: 435px;
        }
    }
}